@import url('https://fonts.googleapis.com/css2?family=Outfit:wght@400;700&display=swap');

* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

body {
  margin: 0;
  font-family: 'Outfit', 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

p {
  font-size: 1.2rem;
}

h1 {
  font-size: 6rem;
}

.App {
  width: 100%;
  height: 100vh;
  position: relative;
  background-color: rgba(0,0,0,0.4);
  color: #fff;
}

.App:before {
  content: '';
  background: url('./assets/bg.jpg') no-repeat center center/cover;
  position: absolute;
  width: 100%;
  height: 100%;
  top: 0;
  left: 0;
  z-index: -1;
}

.App .search {
  display: flex;
  justify-content: center;
  gap: 1rem;
  align-items: center;
  text-align: center;
  padding: 1rem;
}

.App input {
  padding: .7rem 1.5rem;
  font-size: 1.2rem;
  border-radius: 25px;
  border: 1px solid rgba(255,255,255,0.7);
  background: rgba(255,255,255,0.1);
  color: #f8f8f8;
}

::placeholder {
  color: #f8f8f8;
}

.container {
  max-width: 700px;
  margin: auto;
  padding: 0 1rem;
  position: relative;
  top: 10%;
  display: flex;
  flex-direction: column;
  gap: 7rem;
}

.App .top {
  width: 100%;
  margin: 1rem auto;
}

.error {
  position: absolute;
  left: 0;
  right: 0;
  text-align: center;
  color: #E01022;
}

.geolocation {
  position: relative;
  border: none;
  background: inherit;
  width: 2.5rem;
  height: 2.5rem;
  cursor: pointer;
  bottom: 0;
  transition: all .2s ease-in-out;
}

.geolocation:hover {
  bottom: 2px;
}

.geolocation_icon {
  width: 2rem;
  height: 2rem;
}

.temp {
  display: flex;
}

.App .bottom {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-evenly;
  text-align: center;
  width: 100%;
  margin: 2rem auto;
  padding: 1rem 0;
  border-radius: 12px;
  background-color: rgba(255,255,255, 0.2);
}

.App .bottom div {
  flex-basis: 33.3%;
}

.bold {
  font-weight: 700;
}